footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.main-footer-container {
  width: 100%;
  background-color: rgb(194, 224, 245);
  border-radius: 15px 15px 0 0;
  height: 80px;

}
.data-container {
  display: grid;
  grid-template-columns: 65% 35%;
  padding-right: 20px;
  height:inherit;
}
.social-media-container {
  display: flex;
  justify-content: flex-end;
  gap: 20px;
  align-items: center;
}
li {
  list-style-type: none;
}