.general-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.box-container {
  background-color: rgb(194, 224, 245);
  width:fit-content;
  padding: 0px 30px 60px;
  border-radius: 25px;
}
.input-container {
  display: flex;
  flex-direction: column;
  width: auto;
}
.sign-in-button-container {
  display: flex;
  padding-top: 30px;
  width: auto;
  justify-content: center;
}
.signin-button {
  background-color: rgb(194, 224, 245);
  border: 1px solid rgb(36, 50, 245);
  border-radius: 10px;
  height: 35px;
  min-width: calc(50%);
  font-family:'Times New Roman', Times, serif;
  font-size: 16px;
  padding: 5px;
  font-weight: bold;
}
.signin-button:hover {
  cursor: pointer;
}