.main-div-container {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  margin-left: 25px;
}
.general-main-username-container {
  height: 50px;
}
.general-main-container {
  display: flex;
  justify-content: center;
  margin-top: 25px;
}
.big-container {
  width: fit-content;
  text-align: center;
}
.big-container-username:hover {
  background-color: rgb(118, 191, 244);
}
.big-container-username, .big-container {
  background-color: rgb(194, 224, 245);
  padding: 10px;
  border-radius: 25px;
}
.user-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 10px;
  width: 400px;
  margin-top: 25px;
}
.user-username {
  text-align: center;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 20px;
}
p, h3 {
  border-radius: 10px;
  padding: 2px 5px;
  margin: 0;
}
.users-title{
  text-align: center;
  margin-bottom: 35px;
  text-shadow: 2px 2px #74e5e0;
}
.link-to-user {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.999);
}
.link-to-user:hover {
  font-size: 300%;
  color: rgb(64, 43, 43);
}