form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: fit-content;
  background-color: rgb(194, 224, 245);
  padding: 25px 45px;
  border-radius: 10px;
}
.input-data-container {
  display: flex;
  flex-direction: column;
}
.main-container {
  display: flex;
  justify-content: center;
  padding-top: 15px;
  margin-bottom: 100px
}
.signup-title {
  font-weight: 700;
}
.submit-button-container {
  display: flex;
  padding-top: 30px;
  width: auto;
  justify-content: center;
}
.create-user-button {
  background-color: rgb(194, 224, 245);
  border: 1px solid rgb(36, 50, 245);
  border-radius: 10px;
  height: 35px;
  min-width: calc(50%);
  font-family:'Times New Roman', Times, serif;
  font-size: 16px;
  padding: 5px;
  font-weight: bold;
}
.create-user-button:hover {
  cursor: pointer;
}